export default class GlobalTemplate {

    constructor() {

        this.json = {
            "title": "Nouveau",
        
            "scene" : {
        
                "data": [],
                
                "dataProcessing": [],
        
                "objects" : [],
        
                "actions": []
        
            },
            
            "video": {
        
                "settings" : {
                    "fps": 60,
                    "duration": 15000,
                    "width": 1920,
                    "height": 1080
                },
        
                "cameraMoves": []
        
            }
        
        }

    }

    generate( needs ) {

        let heatMap = false;
        let heatMapData = [];

        for ( let data of needs.Data ) {

            data.name = data.name ?? "Data " + Math.floor( Math.random() * 9999 );

            let newData = {
                name: data.name,
                information: {
                    fileName: data.name + '.' + data.type.toLowerCase(),
                    type: data.type,
                    settings: {
                    }
                }
            }

            this.json.scene.data.push( newData );


            switch ( needs.DataObject ) {

                case "Lines" : {

                    this.json.scene.objects.push( {
                        name: "lines" + data.name,
                        information: { 
                            type: "Lines", 
                            dataName: data.name, 
                            settings: {    
                                color: "#2247DB",  
                                opacity: 0.03, 
                                bloom: false   
                            }
                        }
                    } );

                    break;

                }

                case "PointFlow" : {

                    this.json.scene.objects.push( {
                        name: "PF" + data.name,
                        information: {
                            type: "PointFlow",
                            dataName: data.name,
                            settings: {
                                color: "#2247DB",
                                opacity: 0.2,
                                point_size: 5
                            }
                        }
                    } );

                    break;

                }

                case "HMHexa" : {

                    heatMap = true;
                    heatMapData.push( data.name );

                    break;

                }

                default: {
                    break;
                }
                
            }

        }

        if ( heatMap ) {
            
            this.json.scene.dataProcessing.push( {
                name: "Density Map",
                information: {
                    type: "Density_Map_2D",
                    data: {
                        init: heatMapData[ 0 ],
                        add: heatMapData.slice( 1 ),
                    },
                    settings: {
                        densityLat: 400,
                        kernelSize: 10,
                        computeDirection: false
                    }
                }
            } );

            this.json.scene.objects.push( {
                name: "heatMap",
                information: {
                    type: "Heat_Map_Hexagon",
                    densityMapName: "Density Map",
                    settings: {
                        colors: [ ["#F2C2C8", 0 ], [ "#F27989", 15 ], [ "#F2495F", 30 ], [ "#F22440", 50 ], [ "#F20C2B", 80 ] ],
                        opacity: [ [ 0, 3 ], [ 1, 15 ], [ 1, 100 ] ],
                        altitude: [ [ 0, 0 ], [ 0, 15 ], [ 2, 100 ] ],
                        optimizeThreshold: 0.03
                    }
                }
            } );

        }

        switch ( needs.Area ) {

            case "World" : {

                this.json.video.cameraMoves.push( {
                    timestep: 0,
                    type: "Jump",
                    to: { "lat": 30, "long": 0, "alt": 7000000 },
                    lookAt: "center"
                } );
                this.json.video.cameraMoves.push( {
                    timestep: 3000,
                    type: "Around",
                    target: { "lat": 90, "long": 0, "alt": 0 },
                    angle: -360,  
                    duration: 20000
                } );

                break;
            }

            case "Europe" : {

                this.json.video.cameraMoves.push( {
                    timestep: 0,
                    type: "Jump",
                    to: { "lat": 30, "long": 0, "alt": 7000000 },
                    lookAt: "center"
                } );
                this.json.video.cameraMoves.push( {
                    timestep: 500,
                    type: "FromTo",
                    duration: 2000,
                    to: { "lat": 48, "long": 8, "alt": 2500000 },
                    lookAt: "center"
                } );
                break;
            }

            case "France" : {

                this.json.video.cameraMoves.push( {
                    timestep: 0,
                    type: "Jump",
                    to: { "lat": 30, "long": 0, "alt": 7000000 },
                    lookAt: "center"
                } );
                this.json.video.cameraMoves.push( {
                    timestep: 500,
                    type: "FromTo",
                    duration: 2000,
                    to: { "lat": 45, "long": 2, "alt": 1000000 },
                    lookAt: "center"
                } );
                break;
            }

            case "CDG" : {

                this.json.video.cameraMoves.push( {
                    timestep: 0,
                    type: "Jump",
                    to: { "lat": 48.5, "long": 2.45, "alt": 200000 },
                    lookAt: "center"
                } );
                this.json.video.cameraMoves.push( {
                    timestep: 6000,
                    type: "FromTo",
                    duration: 2000,
                    to: { "lat": 48.2, "long": 2.7, "alt": 20000 },
                    lookAt: { "lat": 49.3, "long": 2.7, "alt": 20000 },
                } );
                break;
            }

            default: {
                break;
            }

        }

        return this.json;

    }

}