import React from 'react';
import VideoSection from './videoSection';
import DataSection from './data/dataSection';
import DataProcessSection from './dataProcessing/dataProcessSection';
import ObjectSection from './objectSection/objectSection';
import ActionSection from './action/actionSection';
import CameraMoveSection from './cameraMove/cameraMoveSection';
import './jsonIHM.scss';

class JsonIHM extends React.Component {

  constructor( props ) {

    super( props );

    this.state= {
        isCollapsed: {
            video: false,
            data: false,
            dataProcess: false,
            object: false,
            action: false,
            cameraMove: false
        }
    }

  }
  
  expandCollapse = ( section ) => {
      
    let isCollapsed = { ...this.state.isCollapsed };

    switch (section ) {

        case 'video' :
            isCollapsed.video = !isCollapsed.video;
            break;

        case 'data' :
            isCollapsed.data = !isCollapsed.data;
            break;

        case 'dataProcess' :
            isCollapsed.dataProcess = !isCollapsed.dataProcess;
            break;

        case 'object' :
            isCollapsed.object = !isCollapsed.object;
            break;

        case 'action' :
            isCollapsed.action = !isCollapsed.action;
            break;

        case 'cameraMove' :
            isCollapsed.cameraMove = !isCollapsed.cameraMove;
            break;

        default:
            break;

    }

    this.setState( { ...this.state, isCollapsed: isCollapsed } );

  } 

  refactorDataName () {

  }

  refactorDataProcessingName () {
      
  }

  refactorObjectName () {
      
  }

  render() {


    return (
      <div className='json_ihm'>
        <div className='section' id="video">
            <div className='section_title' onClick={ () => this.expandCollapse('video')}>
                <span>🎥</span>
                <span>Video</span>
            </div>
            <VideoSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.video}
            />
        </div>
        <div className='section' id="data">
            <div className='section_title' onClick={ () => this.expandCollapse('data')}>
                <span>📄</span>
                <span>Data</span>
            </div>
            <span></span>
            <DataSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.data}
            />
        </div>
        <div className='section' id="data_process">
            <div className='section_title' onClick={ () => this.expandCollapse('dataProcess')}>
                <span>📝</span>
                <span>Data Processing</span>
            </div>
            <DataProcessSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.dataProcess}
            />
        </div>
        <div className='section' id="object">
            <div className='section_title' onClick={ () => this.expandCollapse('object')}>
                <span>🌍</span>
                <span>Object</span>
            </div>
            <ObjectSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.object}
            />
        </div>
        <div className='section' id="action">
            <div className='section_title' onClick={ () => this.expandCollapse('action')}>
                <span>🔧</span>
                <span>Action</span>
            </div>
            <ActionSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.action}
            />
        </div>
        <div className='section' id="camera_move">
            <div className='section_title' onClick={ () => this.expandCollapse('cameraMove')}>
                <span>🎬</span>
                <span>Camera Move</span>
            </div>
            <CameraMoveSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.cameraMove}
            />
        </div>
      </div>
    )

  }

}


export default JsonIHM;